export function animate(paths, algo, time) {
  let { visitedNodes, path } = paths;
  for (let i = 0; i < visitedNodes.length; i++) {
    if (i === 0) {
      continue;
    }
    setTimeout(() => {
      if (i !== visitedNodes.length - 1) {
        document.getElementById(
          `${visitedNodes[i].row}-${visitedNodes[i].col}`
        ).className = "node visitedNode";
      }
    }, i * time);
    if (algo !== "dfs") {
      setTimeout(() => {
        if (i === visitedNodes.length - 1) {
          animatePath(path, time);
        }
      }, i * time);
    }
  }
}
function animatePath(path, time) {
  if (path.length === 0) {
    alert("No Way found!");
  }
  for (let i = 0; i < path.length; i++) {
    if (i === 0) {
      continue;
    }
    setTimeout(() => {
      document.getElementById(`${path[i].row}-${path[i].col}`).className =
        "node path";
    }, i * time);
    if (i === 1) {
      if (path[i].row > path[0].row) {
        document.getElementById(
          `${path[0].row}-${path[0].col}`
        ).style.transform = "rotate(0deg)";
      } else if (path[i].row < path[0].row) {
        document.getElementById(
          `${path[0].row}-${path[0].col}`
        ).style.transform = "rotate(180deg)";
      } else if (path[i].col > path[0].col) {
        document.getElementById(
          `${path[0].row}-${path[0].col}`
        ).style.transform = "rotate(90deg)";
      } else if (path[i].col < path[0].col) {
        document.getElementById(
          `${path[0].row}-${path[0].col}`
        ).style.transform = "rotate(270deg)";
      }
    }
  }
}
