import React from "react";
import "./node.css";

export default function node(props) {
  let className = props.isStartNode
    ? "node startNode"
    : props.isFinishNode
    ? "node finishNode"
    : props.isWall
    ? "node wall"
    : "node";
  return (
    <div
      onDragStart={() => props.dragged(props.row, props.col)}
      onDragEnd={() => props.dragEnded()}
      onDragLeaveCapture={() => props.dragLeaved(props.row, props.col)}
      onMouseUp={() => props.mouseUp()}
      onMouseDown={() => props.mouseDown(props.row, props.col)}
      onMouseEnter={() => props.mouseEnter(props.row, props.col)}
      id={`${props.row}-${props.col}`}
      className={className}
    ></div>
  );
}
