import React, { Component } from "react";
import "./board.css";
import Nav from "../Navbar/Navbar";
import Node from "../Node/node";
import Option from "../Navbar/option";
import { Dijkstra } from "../Algorithms/Dijkstra";
import { animate } from "../animate";
import { Dfs } from "../Algorithms/dfs";

export default class Board extends Component {
  constructor(props) {
    super(props);
    this.state = {
      board_array: [],
      mouseIsEntered: false,
      size: window.innerWidth <= 700 ? 22 : 28,
      time: 20,
      dragging: "",
      startNode: {
        m: Math.floor(
          Math.floor(window.innerWidth / (window.innerWidth <= 700 ? 22 : 28)) /
            2 -
            Math.floor(
              window.innerWidth / (window.innerWidth <= 700 ? 22 : 28)
            ) /
              4
        ),
        n: Math.floor(
          Math.floor(
            (window.innerHeight - 170) / (window.innerWidth <= 700 ? 22 : 28)
          ) / 2
        ),
      },
      finishNode: {
        m: Math.floor(
          Math.floor(window.innerWidth / (window.innerWidth <= 700 ? 22 : 28)) /
            2 +
            Math.floor(
              window.innerWidth / (window.innerWidth <= 700 ? 22 : 28)
            ) /
              4
        ),
        n: Math.floor(
          Math.floor(
            (window.innerHeight - 170) / (window.innerWidth <= 700 ? 22 : 28)
          ) / 2
        ),
      },
      m: Math.floor(window.innerWidth / (window.innerWidth <= 700 ? 22 : 28)),
      n: Math.floor(
        (window.innerHeight - 170) / (window.innerWidth <= 700 ? 22 : 28)
      ),
      clearDisabled: false,
    };
    window.addEventListener("resize", async () => {
      let w = 28;
      if (window.innerWidth <= 700) {
        w = 22;
      }
      this.setState({
        startNode: {
          m: Math.floor(
            Math.floor(window.innerWidth / w) / 2 -
              Math.floor(window.innerWidth / w) / 4
          ),
          n: Math.floor(Math.floor((window.innerHeight - 170) / w) / 2),
        },
        finishNode: {
          m: Math.floor(
            Math.floor(window.innerWidth / w) / 2 +
              Math.floor(window.innerWidth / w) / 4
          ),
          n: Math.floor(Math.floor((window.innerHeight - 170) / w) / 2),
        },
        m: Math.floor(window.innerWidth / w),
        n: Math.floor((window.innerHeight - 170) / w),
        size: w,
      });
      this.reset();
    });
  }
  componentDidMount() {
    this.reset();
  }
  reset() {
    let m = this.state.m;
    let n = this.state.n;
    this.setState({
      isMouseEntered: false,
    });
    let array = [];
    for (let i = 0; i < m; i++) {
      let m_array = [];
      for (let j = 0; j < n; j++) {
        m_array.push({
          row: i,
          col: j,
          isWall: false,
          isStartNode:
            i === this.state.startNode.m && j === this.state.startNode.n,
          isFinishNode:
            i === this.state.finishNode.m && j === this.state.finishNode.n,
          previous: null,
          distance: Infinity,
          visited: false,
        });
      }
      array.push(m_array);
    }
    this.setState({
      board_array: array,
      mouseIsEntered: false,
    });
  }
  clear() {
    let m = this.state.m;
    let n = this.state.n;
    let array = [];
    for (let i = 0; i < m; i++) {
      let m_array = [];
      for (let j = 0; j < n; j++) {
        m_array.push({
          row: i,
          col: j,
          isWall: false,
          isStartNode:
            i === this.state.startNode.m && j === this.state.startNode.n,
          isFinishNode:
            i === this.state.finishNode.m && j === this.state.finishNode.n,
          previous: null,
          distance: Infinity,
          visited: false,
        });
        if (i === this.state.startNode.m && j === this.state.startNode.n) {
          document.getElementById(`${i}-${j}`).className = "node startNode";
        } else if (
          i === this.state.finishNode.m &&
          j === this.state.finishNode.n
        ) {
          document.getElementById(`${i}-${j}`).className = "node finishNode";
        } else {
          document.getElementById(`${i}-${j}`).className = "node";
        }
      }
      array.push(m_array);
    }
    this.setState({
      isMouseEntered: false,
      board_array: array,
    });
  }

  plotWall(m, n) {
    let array = this.state.board_array;
    let node = array[m][n];
    let newNode = {
      ...node,
      isWall: !node.isWall,
    };
    array[m][n] = newNode;
    return array;
  }
  mouseDown(m, n) {
    let newArray = this.plotWall(m, n);
    this.setState({
      board_array: newArray,
      mouseIsEntered: true,
    });
  }
  mouseUp() {
    this.setState({
      mouseIsEntered: false,
    });
  }
  mouseEnter(m, n) {
    if (!this.state.mouseIsEntered) return;
    let newArray = this.plotWall(m, n);
    this.setState({
      board_array: newArray,
    });
  }
  run(algo, algoSelected) {
    if (algoSelected === false) {
      return;
    }
    if (algo === " ") {
      return;
    } else if (algo === "Dijkstra") {
      this.setState({
        clearDisabled: true,
      });
      let dijkstra = Dijkstra(
        this.state.startNode,
        this.state.finishNode,
        this.state.board_array
      );
      console.log(this.state.time);
      animate(dijkstra, "dijkstra", this.state.time);
      let { visitedNodes, path } = dijkstra;
      setTimeout(() => {
        this.setState({
          clearDisabled: false,
        });
      }, visitedNodes.length * 10 + path.length * 10);
    } else if (algo === "Breath First Search") {
      console.log(algo);
    } else if (algo === "Depth First Search") {
      let dfs = Dfs(
        this.state.startNode,
        this.state.finishNode,
        this.state.board_array
      );
      animate(dfs, "dfs", this.state.time);
      setTimeout(() => {
        this.setState({
          clearDisabled: false,
        });
      }, dfs.length);
    }
  }
  dragged(row, col) {
    this.setState({
      isMouseEntered: false,
    });
    if (row === this.state.startNode.m && col === this.state.startNode.n) {
      this.setState({
        dragging: "startNode",
      });
      let array = this.state.board_array;
      array[row][col].startNode = false;
      this.setState({
        board_array: array,
      });
    } else if (
      row === this.state.finishNode.m &&
      col === this.state.finishNode.n
    ) {
      this.setState({
        dragging: "finishNode",
      });
      let array = this.state.board_array;
      array[row][col].finishNode = false;
      this.setState({
        board_array: array,
      });
    } else {
      return;
    }
  }

  dragLeaved(row, col) {
    let array = this.state.board_array;
    let node = array[row][col];
    if (this.state.dragging === "startNode") {
      if (this.state)
        document.getElementById(
          `${this.state.startNode.m}-${this.state.startNode.n}`
        ).className = "node";
      let newNode = {
        ...node,
        startNode: true,
      };
      node = newNode;
      array[row][col] = newNode;
      this.setState({
        startNode: {
          m: row,
          n: col,
        },
        board_array: array,
        mouseIsEntered: false,
      });
      document.getElementById(`${row}-${col}`).className = "startNode node";
    } else if (this.state.dragging === "finishNode") {
      document.getElementById(
        `${this.state.finishNode.m}-${this.state.finishNode.n}`
      ).className = "node";
      let newNode = {
        ...node,
        finishNode: true,
      };
      node = newNode;
      array[row][col] = newNode;
      this.setState({
        finishNode: {
          m: row,
          n: col,
        },
        board_array: array,
        mouseIsEntered: false,
      });
      document.getElementById(`${row}-${col}`).className = "finishNode node";
    }
  }
  dragEnded() {
    this.setState({
      dragging: "",
    });
  }
  timeSet(time) {
    if (time === "slow") {
      this.setState({
        time: 30,
      });
    } else if (time === "medium") {
      this.setState({
        time: 20,
      });
    } else if (time === "fast") {
      console.log(time, "fast");
      this.setState({
        time: 10,
      });
    }
  }
  render() {
    return (
      <React.Fragment>
        <Nav
          clear={() => this.clear()}
          run={(algo, algoSelected, time) => this.run(algo, algoSelected)}
          clearDisabled={this.state.clearDisabled}
          timeSet={(time) => this.timeSet(time)}
        />
        <Option />
        <div className="col">
          {this.state.board_array.map((row, rowIdx) => {
            return (
              <div key={rowIdx} className="c">
                {row.map((col, colIdx) => {
                  return (
                    <Node
                      row={rowIdx}
                      col={colIdx}
                      key={rowIdx - colIdx}
                      mouseUp={() => this.mouseUp()}
                      mouseEnter={(m, n) => this.mouseEnter(m, n)}
                      mouseDown={(m, n) => this.mouseDown(m, n)}
                      isWall={col.isWall}
                      isStartNode={col.isStartNode}
                      isFinishNode={col.isFinishNode}
                      dragged={(row, col) => this.dragged(row, col)}
                      dragLeaved={(row, col) => this.dragLeaved(row, col)}
                      dragEnded={() => this.dragEnded()}
                    />
                  );
                })}
              </div>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}
