export function Dijkstra(startNode, finishNode, map) {
  let visitedNodes = [];
  map[startNode.m][startNode.n].distance = 0;
  let nodes = getAllNodes(map);
  while (nodes.length > 0) {
    nodes.sort((a, b) => {
      return a.distance - b.distance;
    });
    let currentNode = nodes.shift();
    if (currentNode.isWall) {
      continue;
    }
    if (!currentNode.visited) {
      visitedNodes.push(currentNode);
    }
    if (currentNode.distance === Infinity) {
      let path = [];
      return { visitedNodes, path };
    }
    if (currentNode.row === finishNode.m && currentNode.col === finishNode.n) {
      let path = shortestPath(currentNode);
      return { visitedNodes, path };
    }
    let neighbors = getNeighbors(currentNode, map);
    neighbors.forEach((neighbor) => {
      if (!neighbor.isWall) {
        updateDistance(currentNode, neighbor);
      }
    });
  }
  let path = shortestPath(map[finishNode.m][finishNode.n]);
  return { visitedNodes, path };
}

function getAllNodes(map) {
  let newMap = [];
  for (let i = 0; i < map.length; i++) {
    for (let j = 0; j < map[0].length; j++) {
      newMap.push(map[i][j]);
    }
  }
  return newMap;
}

function getNeighbors(currentNode, map) {
  let neighbors = [];
  if (currentNode.row > 0) {
    neighbors.push(map[currentNode.row - 1][currentNode.col]);
  }
  if (currentNode.row < map.length - 1) {
    neighbors.push(map[currentNode.row + 1][currentNode.col]);
  }
  if (currentNode.col > 0) {
    neighbors.push(map[currentNode.row][currentNode.col - 1]);
  }
  if (currentNode.col < map[0].length - 1) {
    neighbors.push(map[currentNode.row][currentNode.col + 1]);
  }
  return neighbors;
}

function updateDistance(currentNode, neighbor) {
  if (currentNode.distance + 1 < neighbor.distance) {
    neighbor.distance = currentNode.distance + 1;
    neighbor.previous = currentNode;
  }
}

function shortestPath(finishNode) {
  let path = [];
  let pre = finishNode.previous;
  while (pre !== null) {
    path.push(pre);
    pre = pre.previous;
  }
  return path.reverse();
}
