import React from "react";
import "./option.css";

export default function option() {
  return (
    <div className="option-container">
      <div className="title map">Map</div>
      <div className="options">
        <div className="item">
          <div className="node startNode i"></div> : StartNode
        </div>
        <div className="item">
          <div className="node finishNode i"></div> : FinishNode
        </div>
        <div className="item">
          <div className="node unvisitedNode i"></div> : unvisitedNode
        </div>
        <div className="item">
          <div className="node visitedNode i"></div> : visitedNode
        </div>
        <div className="item">
          <div className="node wall i"></div> : Wall
        </div>
        <div className="item">
          <div className="node path i"></div> : Path
        </div>
      </div>
    </div>
  );
}
