import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Navbar, NavDropdown } from "react-bootstrap";
import "./Navbar.css";

export default function Nav_bar(props) {
  const [algorithm, setAlgorithm] = useState("");
  const [algoSelected, setAlgoSelected] = useState(false);
  const [canSet, setCanSet] = useState(true);
  useEffect(() => {
    if (props.clearDisabled) {
      document.getElementById("clearDisabled").classList.add("disabled");
      document.getElementById("run").classList.add("disabled");
      setAlgoSelected(false);
      setCanSet(false);
    } else {
      document.getElementById("clearDisabled").classList.remove("disabled");
    }
  }, [algoSelected, props]);
  function setAlgo(algo) {
    document.getElementById("run").classList.remove("disabled");
    setAlgorithm(algo);
    if (canSet) {
      setAlgoSelected(true);
    } else {
      setAlgoSelected(false);
      document.getElementById("run").classList.add("disabled");
    }
  }
  function setTime(time) {
    props.timeSet(time);
  }
  return (
    <div>
      <Navbar className="navbar" variant="k" expand="lg">
        <div className="cont">
          <div>
            <Navbar.Brand
              className="title"
              style={{ marginLeft: "20px", color: "white" }}
              href="#home"
            >
              Pathfinding Visualizer
            </Navbar.Brand>
          </div>
          <div className="buttons">
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <button
                className="list button"
                id="clearDisabled"
                onClick={() => {
                  setCanSet(true);
                  if (props.clearDisabled) {
                    return;
                  }
                  props.clear();
                  if (algorithm === "") {
                    setAlgoSelected(false);
                  } else if (algorithm !== "") {
                    setAlgoSelected(true);
                    document.getElementById("run").classList.remove("disabled");
                  }
                }}
              >
                Clear
              </button>
              <button
                className="list disabled"
                id="run"
                onClick={() => props.run(algorithm, algoSelected)}
              >
                Run {algorithm}!
              </button>
              <div style={{ display: "flex", flexDirection: "row" }}>
                <NavDropdown title={"Algorithms"} id="nav-dropdown">
                  <NavDropdown.Item className="dropdown-list" disabled="true">
                    Select an Algorithm
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setAlgo("Dijkstra")}
                  >
                    Dijkstra
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setAlgo("Breath First Search")}
                  >
                    Breath First Search
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setAlgo("Depth First Search")}
                  >
                    Depth First Search
                  </NavDropdown.Item>
                </NavDropdown>
                <NavDropdown title={"Time"} id="nav-dropdown">
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setTime("fast")}
                  >
                    Fast
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setTime("medium")}
                  >
                    Medium
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    className="dropdown-list"
                    onClick={() => setTime("slow")}
                  >
                    Slow
                  </NavDropdown.Item>
                </NavDropdown>
              </div>
            </div>
          </div>
        </div>
      </Navbar>
    </div>
  );
}
